@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700;800;900&display=swap);
nav {
    background: var(--background-color-primary) !important;
    color: var(--primary-color);
    padding: 1.75rem !important;
    margin-bottom: 2rem !important;
}

nav h1 {
    margin: 0; font-size: 3rem;
    display: flex; align-content: center;
}
nav h1 svg {
    margin-right: .5rem;
    font-size: 3.7rem;
}
form#addcontact h2 {
    text-align: center; margin-bottom: 1.6rem;
    color: var(--primary-color);
    font-size: 2.4rem; font-weight: 600; 
}
form#addcontact input.form-control {
    font-size: 1.75rem; height: 3.5rem;
    margin: 1.92rem 0;
}
form#addcontact input:required {
    box-shadow: none;
}
form#addcontact input.form-control:focus {
    box-shadow: none;
    border-color: var(--primary-color);
}
form#addcontact h5 {
    font-size: 1.6rem;
}
form#addcontact .form-check {
    margin: 1.92rem 0;
}
form#addcontact .form-check-label {
    font-size: 1.4rem; font-weight: 500;
    margin-right: .5rem;
}
form#addcontact button.btn {
    padding: .75rem 1.5rem;
    font-size: 1.5rem;
}
form#findcontact .form-group {
    margin: 0;
}
form#findcontact .form-group input {
    font-size: 1.75rem; height: 3.5rem;
    margin: 1.92rem 0;
}
form#findcontact input.form-control:focus {
    box-shadow: none;
    border-color: var(--primary-color);
}
.card {
    border-radius: .25rem; margin: 2rem 0;
}
.card-header {
    display: flex; justify-content: space-between; align-items: center;
    border: none !important;
}
.card .card-header h4 {
    margin: 0; color: var(--primary-color);
    font-size: 1.85rem; font-weight: 600;
}
.card .card-header .badge {
    display: flex; justify-content: center; align-content: center;
    padding: .75rem; font-size: 1.6rem; font-weight: 600;
}
.card-footer {
    border: none !important;
}
.card-body {
    font-size: 1.75rem;
}
.card-body li {
    display: flex; align-items: center;
}
.card-body li svg {
    margin-right: .5rem;
}
.card-footer button {
    padding: .5rem 1rem; font-size: 1.5rem;
}
.card-footer button:first-child {
    margin-right: .5rem;
}
.card-header, .card-body, .card-footer {
    background: var(--background-color-secondary) !important;
}
footer {
    background: var(--background-color-primary) !important;
    color: var(--primary-color);
    padding: 1.75rem !important;
    font-size: 1.75rem; font-weight: 500;
    text-align: center;
    margin-top: 2rem !important;
}
footer p {
    margin: 0;
}
footer span {
    color: cornsilk;
}
:root {
  --primary-color: #03a9f4;
  --background-color-primary: #343a40;
  --background-color-secondary: rgba(0, 0, 0, .03);
}

* {
  margin: 0; padding: 0;
  box-sizing: border-box;
}

body, html {
  font-family: 'Maven Pro', sans-serif !important;
  font-size: 10px !important;
  line-height: 1.6 !important;
  background: #fff;
}

ul {
  list-style: none; margin: 0 !important;
}

